body {
  margin: 0;
  padding: 0;
  background-image: url(/memory-clicker/static/media/background.395e5325.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
    display: flex;
    flex-direction: column;
}

.nav-container {
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    height: 80px;
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    color: aquamarine;
    background-color: rgb(0, 0, 0, 0.5);
    font-size: 40px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    box-shadow: 3px 6px 45px 12px rgba(0,0,0,0.9);
}

.directions-container {
   padding-top: 150px;
    height: 320px;
    background-color: rgba(219, 224, 223, 0.5);
    font-size: 60px;
    font-weight: 150;
    text-align: center;
}

.directions-title {
    font-size: 75px;
    margin: 0;
}

.gif-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 100px;
}

.gif {
    flex: 1 0 18%;
    border: 10px solid rgb(0, 0, 0, 0.5);
    margin: 15px;
}

.gif:hover {
    box-shadow: 3px 6px 45px 12px rgba(0,0,0,0.9);
}

footer {
    color: rgba(0,0,0,0.7);
    background-color: rgb(127, 255, 212, 0.7);
    box-shadow: 3px 6px 45px 12px rgba(0,0,0,0.9);
    text-align: end;
    padding: 10px;
    font-size: 20px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.animate-message {
    -webkit-animation-name: message; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
    animation-name: message;
    animation-duration: 1s;
  }
  
  /* Safari 4.0 - 8.0 */
  @-webkit-keyframes message {
    from {color: green;}
    to {color: aqua;}
  }
  
  /* Standard syntax */
  @keyframes message {
    from {color: green;}
    to {color: aqua;}
  }

